import React,{useEffect,useState} from "react";
import {useWallet} from 'use-wallet';
import Swapform from "./swapform.js";
import {Grid} from '@material-ui/core';
import {useApplicationContext} from '../../contexts';
import Axios from "axios";
import loadingScreen from "../../assets/img/box.gif";
import {fantomTestnetAtari,fantomTestnetBridge,mainnetAtari,mainnetBridge,bscAtari,bscBridge,maticAtari,maticBridge,fantomAtari,fantomBridge} from "../../bridgeContract"
import {ethers} from "ethers"
import AlertModal from "../alertModal"



function BridgeCard (){

    const wallet = useWallet();
    const [signer,setSigner] = useState();
    const [balance,setBalance] = useState("0");
    const [fromAmount,setFromAmount] = useState("0");
    const [toAmount,setToAmount] = useState("0");

    const {chainId,updateChainId} = useApplicationContext();
    const [chainId2,setChainId2] = useState("56");
    const [txDatas,setTxDatas] = useState({feeRate:{1:0,56:0,137:0,250:0}});
    
    const [loading, setLoading] = useState(false);
    const [error,setError] = useState(false);
    
    const [pending,setPending] = useState("");
    const [pendingData,setPendingData] = useState({});
    const [alertInfos, setAlertInfos] = useState({title:"text",info:"error"});
    const [alertOpen, setAlertOpen] = useState(false);

    var timeClock;

    const supportedAtari = {
        1:"Atari(ETH)",
        56:"Atari(BSC)",
        137:"Atari(MATIC)",
        250:"Atari(FTM)",
        4002:"Atari(FTMTEST)"
    }

    const explorer = {
        1:"https://etherscan.io/tx/",
        56:"https://bscscan.com/tx/",
        137:"https://polygonscan.com/tx/",
        250:"https://ftmscan.com/tx/"
    }

            
    const PendingCard = (props)=>{
        const {amount,fromChainId,toChainId,fromhash,tohash} = props;
        return (
            <div className = "x-font3 pendingCard">
                <div>{fromhash===undefined?<div>Pending</div>:<a href = {explorer[fromChainId]+fromhash} target = "_blank" rel="noreferrer" >completed Transaction</a>}</div>
                {tohash===undefined?<div>Pending</div>:<a href = {explorer[toChainId]+tohash} target = "_blank" rel="noreferrer" >completed Transaction</a>}
            </div>
        )
    }

    const AlertCard = ()=>{
        return (
            <div className = "AlertCard" onClick={()=>{ console.log("transaction "); setAlertOpen(true);}}>
                {pending==="completed"?"completed Transaction":"Pending Transaction"}
            </div>
            )
    }

    const contracts = {
        1:{atari:mainnetAtari,bridge:mainnetBridge},
        56:{atari:bscAtari,bridge:bscBridge},
        137:{atari:maticAtari,bridge:maticBridge},
        250:{atari:fantomAtari,bridge:fantomBridge},
        4002:{atari:fantomTestnetAtari,bridge:fantomTestnetBridge}
    }

    const getUserData = async ()=>{
        if(wallet.status ==="connected"){
            const provider = new ethers.providers.Web3Provider(wallet.ethereum);
            const signer =await provider.getSigner();
            setSigner(signer);

            var AtariContract = contracts[chainId].atari;
            var signedAtariContract = AtariContract.connect(signer);

            let balance = await signedAtariContract.balanceOf(wallet.account)
            .catch(err=>{
                console.log(err)
            });
            
            if(balance!==undefined){
                setBalance(balance.toString());
            }
        }
    }

    useEffect(()=>{
        getUserData();
    },[wallet.status,chainId])
    
    useEffect(()=>{
        if(Number(balance) < Number(fromAmount)){
            setError(true)
        }
        else{
            setError(false)
        }
    },[fromAmount])

    //bridge Data
    const getData = async()=>{
        let txDatas = await Axios.get("/api/getData");
        console.log(txDatas.data);
        setTxDatas(txDatas.data);
    }

    const checkTxs = (fromChainId)=>{
        if(wallet.status==="connected"&&txDatas.pendingTransaction!==undefined){

            
            txDatas.completedTransaction[fromChainId].map((pendingTx,index)=>{
                //pending
                if(pendingTx.from===wallet.account){
                    let pending = "completed";
                    let pendingData = pendingTx;
                    setPending(pending);
                    setPendingData(pendingData);
                }
            })

            txDatas.pendingTransaction[fromChainId].map((pendingTx,index)=>{
                //pending
                if(pendingTx.from===wallet.account){
                    let pending = "pending";
                    let pendingData = pendingTx;
                    setPending(pending);
                    setPendingData(pendingData);
                }
            })

            txDatas.conformedTransaction[fromChainId].map((pendingTx,index)=>{
                //pending
                if(pendingTx.from===wallet.account){
                    let pending = "conformed";
                    let pendingData = pendingTx;
                    setPending(pending);
                    setPendingData(pendingData);
                }
            })
        }
    }

    useEffect(()=>{
        if(pending!==""){
            setAlertInfos(
                {
                title:pending,
                info:(<PendingCard 
                    fromChainId = {pendingData.fromChainId} 
                    toChainId = {pendingData.toChainId}
                    fromhash = {pendingData.fromhash}
                    tohash = {pendingData.tohash}
                    />)
                })
        }
    },[pending])

    useEffect(()=>{
        checkTxs(1);
        checkTxs(56);
        checkTxs(137);
        checkTxs(250);
    },[txDatas])

    useEffect(()=>{
       setInterval(() => {
            console.log('This will run every second!');
            getData();
          }, 5000);
    },[])

    // exchange amount handle
    const handleAmount = (e)=>{
        setFromAmount(e.target.value);
        let toAmount = Number(e.target.value) - txDatas.feeRate[chainId2];
        if(toAmount<0) toAmount = 0;
        setToAmount(toAmount);
    }

    const handleAmount2 = (e)=>{
        setToAmount(e.target.value);
        let fromAmount = Number(e.target.value) + txDatas.feeRate[chainId2];
        setFromAmount(fromAmount);
    }

    useEffect(()=>{
        let toAmount = Number(fromAmount) - txDatas.feeRate[chainId2];
        if(toAmount<0) toAmount = 0;
        setToAmount(toAmount);
    },[chainId2]);

    const handleChain2Change = (e)=>{
        setChainId2(e.value);
    }
    
    //handle exchange
    const handleExchange =async ()=>{
        if(wallet.status ==="connected"){
            setLoading(true);
            const provider = new ethers.providers.Web3Provider(wallet.ethereum);
            const signer =await provider.getSigner();
            var userAddress =await signer.getAddress();
            let bridgeAddress = (contracts[chainId].bridge).address

            let signedAtarCoin = (contracts[chainId].atari).connect(signer);
            var allowance = (await signedAtarCoin.allowance(userAddress,bridgeAddress)).toString();

            if(allowance<fromAmount){
                var tx = await signedAtarCoin.approve(bridgeAddress,fromAmount*10)
                .catch((err)=>{
                    //console.log(err);
                    setLoading(false)
                });
                if(tx!=null){
                    await tx.wait();
                    exchange();
                }
            }
            else {
                exchange();
            }
        }
    }

    const exchange =async ()=>{
        const provider = new ethers.providers.Web3Provider(wallet.ethereum);
        const signer =await provider.getSigner();
        let signedBridgeContract = (contracts[chainId].bridge).connect(signer);

        var tx = await signedBridgeContract.deposit(fromAmount,chainId2)
        .catch((err) => {
            setLoading(false);
        });
        if(tx!=null) {
            await tx.wait();
            setLoading(false);
            alertInfos.title  = "pending Transaction";
            alertInfos.info = PendingCard;
        }

    }

    const handleClose = ()=>{
        setAlertOpen(false);
    }


    return (
        <div className = "bridgeCard">
        {pending!==""?<AlertCard />
        :""}
        <AlertModal title = {alertInfos.title} info = {alertInfos.info} open = {alertOpen} handleClose = {handleClose}/>
        <span className = "x-font3"> Atari Bridge</span>    
        <div className = "spacer"></div>
        <Grid container>
            <Grid item xs = {12} sm = {12} md = {3} >
            </Grid>
            <Grid item xs = {12} sm = {12} md = {6} style={{border:'1px solid gray', padding:10}}>
                <Swapform token = {supportedAtari[chainId]} amount = {fromAmount} handleAmount = {handleAmount} active = {false}></Swapform>
            </Grid> 
            <Grid item xs = {12} sm = {12} md = {3} >
            </Grid>
            <div className = "spacer"></div>
            <Grid item xs = {12} sm = {12} md = {3} >
            </Grid>
            <Grid item xs = {12} sm = {12} md = {6} style={{border:'1px solid gray', padding:10}}>
                <Swapform token = {supportedAtari[chainId2]} amount = {toAmount} handleAmount = {handleAmount2} handleChainChange = {handleChain2Change} active = {true} ></Swapform>
            </Grid> 
            <Grid item xs = {12} sm = {12} md = {3} >
            </Grid>
        </Grid>
        
        <div className = "spacer-1"></div>
        <button className = "swap-button" onClick = {handleExchange}>{loading?<img src ={loadingScreen}  width = "40px" alt= "loading"/>:error===true?"Insufficient balance":"Exchange"}</button>
        </div>
    )
}

export default BridgeCard;