import React from 'react';
import {useState, useEffect} from "react"
import {Grid} from '@material-ui/core';

import Dropdown from 'react-dropdown';
import ftm_img from '../../assets/img/2.png';
import ether_img from '../../assets/img/ether.png';
import matic_img from '../../assets/img/matic-bg.png';
import bsc_img from '../../assets/img/BNB-bg.png';

function SwapForm (props) {
    
    const {token,  amount, handleAmount, handleChainChange, active} = props;

    const [styledAmount, setStyledAmount] = useState(0)

    useEffect(()=>{
        if(amount !==0)
            setStyledAmount(parseFloat(Number(amount).toFixed(8)));
        else 
            setStyledAmount(amount);

    },[amount]);

    const supportedAtari = {
        1:"Atari(ETH)",
        56:"Atari(BSC)",
        137:"Atari(MATIC)",
        250:"Atari(FTM)",
    }

    const options = [
        { value: 1, label: <div>{supportedAtari[1]}</div> },
        { value: 56, label: <div>{supportedAtari[56]} </div>},
        { value: 137, label: <div>{supportedAtari[137]} </div> },
        { value: 250, label: <div>{supportedAtari[250]} </div>}
      ];

    return ( 
        <div className = "swapForm">
           
            <Grid container>
                <Grid item xs = {12} sm = {12} md = {5} style={{border:'1px solid gray', padding:10, position:'relative'}}>
                    <Dropdown  className = "x-font3 mr-3 bridge-dropdown" options={options} onChange={handleChainChange} value={token} placeholder="Select an option" disabled={!active}/>
                </Grid>
                <Grid item xs = {12} sm = {12} md = {7} style={{border:'1px solid gray', padding:20}}>
                    <input type="number"  step = {0.1} className = "x-stakeCard-input" value={styledAmount} onChange = {handleAmount} />
                </Grid>
            </Grid>
            <span style={{color:'white', marginLeft:10}} >
                
            </span>
        </div>
    )
}

export default SwapForm;